.signup {
    @extend .section-block;
    &__title {
        @extend .site-title;
    }
    &__text {
        @extend .site-text;
    }
    &__form {
        width: 52rem;
        background-color: #fff;
        border-radius: 2.4rem;
        margin: 0 auto;
        box-sizing: border-box;
        padding: 2.4rem 0;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
        }
    }
    &__form-title {
        font-size: 2.4rem;
        color: var(--color-main);
        text-align: center;
        padding-bottom: 2rem;
        margin-bottom: 2rem;
        border-bottom: 0.2rem solid #eee;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.6rem;
        }
    }
    &__rows {
        margin-bottom: 2rem;
        padding: 0 2.4rem;
        label {
            font-size: 2rem;
            color: var(--color-main);
            line-height: 1.6;
            display: block;
            margin-bottom: 0.4rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.6rem;
            }
        }
        input {
            height: 5.6rem;
            line-height: 5.6rem;
            width: 100%;
            background-color: #F5F5F5;
            border-radius: 0.8rem;
            font-size: 2rem;
            color: #333;
            padding: 0 2rem;
            box-sizing: border-box;
            border: 0;
            transition: all 400ms ease;
            &::placeholder {
                color: #9F9F9F;
            }
            &:focus {
                background-color: darken(#F5F5F5,2%);
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.6rem;
                height: 4.8rem;
                line-height: 4.8rem;
            }
        }
    }
    &__btn-submit {
        border-radius: 0.8rem;
        width: 100%;
        background-color: var(--color-main);
        color: #fff;
        font-size: 2.4rem;
        text-align: center;
        border: 0;
        height: 5.6rem;
        line-height: 5.6rem;
        transition: all 400ms ease;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 2rem;
            height: 4.8rem;
            line-height: 4.8rem;
        }
        &:hover {
            background-color: var(--color-main-700);
        }
    }
}