.remind {
    @extend .section-block;
    &__title {
        @extend .site-title;
    }
    &__ol-list {
        list-style-type: decimal;
        padding-left: 2rem;
        li {
            font-size: 2rem;
            line-height: 1.6;
            color: #fff;
            &:not(:last-child) {
                margin-bottom: 0.8rem;
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.6rem;
            }
        }
    }
}