.site-text {
  font-size: 2rem;
  color: #fff;
  line-height: 1.6;
  text-align: center;
  margin-bottom: 2rem;
  font-weight: 500;
  @include susy-breakpoint($s-320, $c8) {
    font-size: 1.6rem;
  }
  @include susy-breakpoint($s-768, $c12) {
    font-size: 1.8rem;
  }
}

.site-title {
  font-size: 2.8rem;
  color: #fff;
  line-height: 1.6;
  text-align: center;
  margin-bottom: 4rem;
  position: relative;
  width: 100%;
  &::before {
    width: 4rem;
    bottom: -1rem;
    left: 50%;
    height: 0.2rem;
    background-color: #fff;
    position: absolute;
    transform: translate3d(-50%,0,0);
    content: "";
    opacity: 0.5;
  }
  &::after {
    margin: 0 0 0 1rem;
  }
  @include susy-breakpoint($s-320, $c8) {
    font-size: 2rem;
  }
  @include susy-breakpoint($s-768, $c12) {
    font-size: 2.4rem;
  }
}

.section-block {
  padding: 8rem 0;
  @include susy-breakpoint($s-320, $c8) {
    padding-top: 6rem;
    padding-bottom: 6rem;
  }
}