.kv {
    position: relative;
    &::after {
        width: 100%;
        height: 50%;
        bottom: 0;
        left: 0;
        position: absolute;
        content: "";
        background: rgb(0,179,164);
        background: linear-gradient(0deg, rgba(0,179,164,1) 13%, rgba(0,179,164,0) 100%);
    }
    &__bg {
        max-width: 100%;
        display: block;
        margin-bottom: 0;
    }
    &__brand {
        position: absolute;
        left: 3rem;
        top: 3rem;
        display: flex;
        @include susy-breakpoint($s-320, $c8) {
            left: 2rem;
            top: 2rem;
        }
    }
    &__logo {
        width: 13.8rem;
        height: 2.7rem;
        margin-right: 3rem;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 13.8rem * 0.6;
            height: 2.7rem * 0.6;
            margin-right: 1.6rem;
        }
    }
    &__dr-wk {
        width: 16.4rem;
        height: 3.8rem;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 16.4rem * 0.6;
            height: 3.8rem * 0.6;
        }
    }
    &__content {
        position: absolute;
        width: 100%;
        top: 50%;
        left: 50%;
        transform: translate3d(-50%,-10%,0);
        z-index: 20;
        @include susy-breakpoint($s-320, $c8) {
            transform: translate3d(-50%,30%,0);
        }
    }
    &__title {
        color: #FFF700;
        font-size: 10.5rem;
        font-weight: 300;
        line-height: 1.2;
        margin-bottom: 4rem;
        text-align: center;
        strong {
            display: block;
            font-size: 12rem;
            font-weight: 700;
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: 10vw;
            margin-bottom: 2rem;
            strong {
                font-size: 11.6vw;
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: 8vw;
            margin-bottom: 2rem;
            strong {
                font-size: 9.2vw;
            }
        }
    }
    &__text {
        @extend .site-text;
        margin-bottom: 0;
    }
}