.app {
    @extend .section-block;
    &__icon {
        width: 20rem;
        height: 20rem;
        display: block;
        margin: 0 auto 2rem;
    }
    &__title {
        @extend .site-title;
    }
    &__text {
        @extend .site-text;
    }
    &__download {
        width: 54rem;
        margin: 0 auto;
        display: grid;
        grid-template-areas: "a b"
                            "c b";
        gap: 1.6rem;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            grid-template-areas: "a""b";
        }
    }
    &__btn-app-store {
        display: block;
        grid-area: a;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
    &__btn-google-play {
        display: block;
        grid-area: c;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
        @include susy-breakpoint($s-320, $c8) {
            grid-area: b;
        }
    }
    &__qrcode {
        grid-area: b;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
        @include susy-breakpoint($s-320, $c8) {
            display: none;
        }
    }
}