.cs {
    @extend .section-block;
    &__text {
        @extend .site-text;
    }
    &__btn-wrap {
        display: flex;
        justify-content: center;
        @include susy-breakpoint($s-320, $c8) {
            flex-wrap: wrap;
        }
    }
    &__btn {
        display: block;
        margin: 0 0.8rem;
        text-align: center;
        img {
            display: block;
            margin: 0 auto;
        }
        span {
            font-size: 2rem;
            color: #fff;
            line-height: 1.6;
        }
        @include susy-breakpoint($s-320, $c8) {
            border: solid 0.2rem #fff;
            flex-basis: 100%;
            padding: 1rem 0;
            &:not(:last-child) {
                margin-bottom: 1rem;
            }
            img {
                display: none;
            }
        }
    }
}