:root {
    font-size: 62.5%;
    --color-main: #00B4A4;
    --color-main-700: #009588;
    --font-size-200: 20rem;
    --font-size-140: 14rem;
    --font-size-80: 8rem;
    --font-size-48: 4.8rem;
    --font-size-40: 4rem;
    --font-size-32: 3.2rem;
    --font-size-30: 3rem;
    --font-size-24: 2.4rem;
    --font-size-20: 2rem;
    --font-size-18: 1.8rem;
    --font-size-16: 1.6rem;
    --font-size-14: 1.4rem;
    --font-size-12: 1.2rem;
}

html {
    font-family: 'Open Sans', 'Noto Sans TC', 'Noto Sans SC', "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", "Helvetica Neue", Helvetica, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    background-color: var(--color-main);
}

.site-footer {
    padding-top: 6rem;
    &__slogan {
        display: block;
        margin:  0 auto 3rem;
        @include susy-breakpoint($s-320, $c8) {
            width: 26rem;
            margin-bottom: 1.6rem;
        }
    }
    &__copyright {
        background-color: var(--color-main-700);
        padding: 2.4rem 0;
        font-size: 1.8rem;
        text-align: center;
        color: #fff;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.2rem;
        }
    }
}